                  <h2 class="body-1 font-weight-medium mt-1">
                    {{
                      productMk.priceBegin === productMk.priceEnd
                        ? `${productMk.priceEnd}฿`
                        : `${productMk.priceBegin}฿ - ${productMk.priceEnd}฿`
                    }}
                    / {{ productMk.nameUnit }}
                  </h2>
                </v-col>
              </v-row>

              <!-- <v-row cols="12" class="pa-2">
                <v-col>
                  <h3 class="indigo--text text--darken-4">มาตรฐาน&nbsp;</h3>
                  <h3 class="body-1 font-weight-medium mt-1">
                    {{ productMk.idTypeOfStand || "-" }}
                  </h3>
                </v-col>
              </v-row> -->
              <v-row cols="12" class="pa-2">
                <v-col cols="6">
                  <h3 class="indigo--text text--darken-4">
                    มาตรฐานการผลิต&nbsp;
                  </h3>
                  <h2
                    v-if="productMk.idTypeOfStand === 'ไม่มี'"
                    class="body-1 font-weight-medium mt-1"
                  >
                    {{ "-" }}
                  </h2>
                  <h2 v-else class="body-1 font-weight-medium mt-1">
                    {{ productMk.idTypeOfStand || "-" }}
                  </h2>
                </v-col>
                <v-col cols="6">
                  <h3 class="indigo--text text--darken-4">
                    การผลิตตามออเดอร์&nbsp;
                  </h3>
                  <h2 class="body-1 font-weight-medium mt-1">
                    {{ productMk.madeByOrder || "-" }}
                  </h2>
                </v-col>
              </v-row>

              <v-row justify="space-around">
                <v-row class="my-3 ml-3">
                  <h3 class="indigo--text text--darken-4">เดือนที่มีสินค้า</h3>
                </v-row>
                <v-col cols="12" sm="10" md="12">
                  <v-chip
                    class="ma-2 green darken-1 white--text"
                    v-for="tag in monthTags"
                    :key="tag.id"
                    :disabled="tag.isDiabled"
