                  >
                    {{ tag.monthName }}
                  </v-chip>
                </v-col>
              </v-row>
            </v-row>
            <v-divider></v-divider>
          </v-col>
        </v-row>
        <v-row class="mt-0 pa-0">
          <v-col
            v-for="(item, index) in productImgMk"
            :key="index"
            class="d-flex child-flex"
            cols="2"
          >
            <expandable-image :src="item.imgTitle" />
            <!-- <v-img
                :src="item.imgTitle"
                :lazy-src="item.imgTitle"
              >
                <template v-slot:placeholder>
                  <v-row
                    class="fill-height ma-0"
                    align="center"
                    justify="center"
                  >
                    <v-progress-circular
                      indeterminate
                      color="grey lighten-5"
                    ></v-progress-circular>
                  </v-row>
                </template>
              </v-img> -->
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row class="mt-2" justify="center" cols="12">
          <v-col cols="6" class="mx-auto pa-5">
            <h2 class="indigo--text text--darken-4 mx-auto mb-3">
              จัดจำหน่ายโดย
            </h2>
            <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >ประเภทองค์กร :</span
              >
              {{ sellerUser.nameTypeOfSeller }}
            </p>
            <!-- <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >ชื่อองค์กร :</span
              >
              {{ sellerUser.groupName || "-" }}
            </p> -->
            <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >ชื่อกลุ่ม :</span
              >
              {{ sellerUser.groupName || "-" }}
              <!-- {{ `${sellerUser.firstName} ${sellerUser.lastName}` }} -->
            </p>
            <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >ที่อยู่ :</span
              >
              บ้านเลขที่ {{ sellerUser.address || "-" }} หมู่
              {{ sellerUser.moo || "-" }} ตำบล
              {{ sellerUser.tamName || "-" }} อำเภอ
              {{ sellerUser.ampName || "-" }} จังหวัด
              {{ sellerUser.provName || "-" }} {{ sellerUser.postCode || " " }}
            </p>
            <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >เบอร์โทรศัพท์ :</span
              >
              {{ sellerUser.phoneNumber || "-" }}
              <!-- {{ `${sellerUser.firstName} ${sellerUser.lastName}` }} -->
            </p>
            <!-- <p class="body-1 font-weight-medium">
              <span class="font-weight-bold indigo--text text--darken-4"
                >พื้นที่โครงการ :</span
              >
              {{ area.areaName }}
            </p> -->
            <v-row cols="12">
              <v-col cols="12">
                <v-img
                  width="24"
                  :src="require('../assets/img/facebook.png')"
                ></v-img>
                <p v-if="sellerUser.facebookName">
                  {{ sellerUser.facebookName }}
                </p>
                <!-- :href="`https://www.facebook.com/${sellerUser.facebookName}`" -->
                <p v-else class="body-1 font-weight-medium">
                  {{ sellerUser.facebookName || "-" }}
                </p>
              </v-col>
              <v-col>
                <v-img
                  width="24"
                  :src="require('../assets/img/line.png')"
                ></v-img>
                <p class="body-1 font-weight-medium">
                  {{ sellerUser.lineName || "-" }}
                </p>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="6">
            <v-row>
              <h2 class="indigo--text text--darken-4 mx-auto">พื้นที่สินค้า</h2>
            </v-row>
            <div style="height: 500px; width: 100%">
              <l-map
                :zoom="zoom"
                :center="center"
                @update:zoom="zoomUpdated"
                @update:center="centerUpdated"
                @update:bounds="boundsUpdated"
                :options="{ scrollWheelZoom: false }"
              >
                <l-tile-layer :url="url"></l-tile-layer>
                <l-marker
                  v-for="marker in markers"
                  :key="marker.id"
                  :visible="marker.visible"
                  :lat-lng.sync="marker.position"
                  :icon="marker.icon"
                  @click="mapLink()"
                >
                  <l-tooltip
                    :content="marker.tooltip"
                    :options="{ permanent: true }"
                  />
                </l-marker>
              </l-map>
            </div>
          </v-col>
        </v-row>
        <v-divider></v-divider>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import axios from "axios";
import { latLng } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup, LTooltip } from "vue2-leaflet";

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
    LTooltip
  },
  inject: ["mySpinner"],
  data() {
    return {
      queryId: null,
      month: [],
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
      productMk: [],
      productImgMk: [],
      sellerUser: [],
      selection: "มกราคม",
      monthTags: [
        { id: 1, monthName: "มกราคม", isDiabled: true },
        { id: 2, monthName: "กุมภาพันธ์", isDiabled: true },
        { id: 3, monthName: "มีนาคม", isDiabled: true },
        { id: 4, monthName: "เมษายน", isDiabled: true },
        { id: 5, monthName: "พฤษภาคม", isDiabled: true },
        { id: 6, monthName: "มิถุนายน", isDiabled: true },
        { id: 7, monthName: "กรกฎาคม", isDiabled: true },
        { id: 8, monthName: "สิงหาคม", isDiabled: true },
        { id: 9, monthName: "กันยายน", isDiabled: true },
        { id: 10, monthName: "ตุลาคม", isDiabled: true },
        { id: 11, monthName: "พฤศจิกายน", isDiabled: true },
        { id: 12, monthName: "ธันวาคม", isDiabled: true }
      ],
      area: {},
      areaText: "",
      // Map Data
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      zoom: 6.5,
      center: [0, 0],
      bounds: null,
      showAmount: true,
      showDetails: false,
      selectedItem: 1,
      target: {},
      markers: [
        {
          id: null,
          position: { lat: null, lng: null },
          tooltip: null,
          visible: true
        }
      ]
      // End Map Data
    };
  },
  methods: {
    // Map
    mapLink() {
      window.open(
        `http://maps.google.com/?q=${this.target.lat},${this.target.long},${this.area.areaName}`,
        "_blank"
      );
    },
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    // End map
    getProductImg(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductImg.php`, formData)
        .then(res => {
          if (res.data.productImgMk !== 0) {
            this.productImgMk = res.data.productImgMk;
            this.productImgMk = this.productImgMk.filter(
              e => e.imgTitle !== this.productMk.titleImg
            );
            console.log("productImgMk", this.productImgMk);
          }
        })
        .catch(err => {
          this.mySpinner.isLoading = false;
          console.log(err);
        });
    },
    getProductDetail(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductDetail.php`, formData)
        .then(res => {
          if (res.data.productMk !== 0) {
            this.productMk = res.data.productMk[0];
            let monthArr = this.productMk.monthOfProduct
              .split(",")
              .map(item => {
                return parseInt(item, 10);
              });
            monthArr.sort((a, b) => {
              return a - b;
            });

            this.monthTags.map((e, i) => {
              monthArr.map(j => {
                if (j === e.id) {
                  e.isDiabled = false;
                }
              });
            });
            console.log("monthTags", this.monthTags);
            console.log("productMk", this.productMk);
            this.mySpinner.isLoading = false;
          }
        })
        .then(() => {
          this.getSellerInfo();
          this.getProductImg(this.queryId);
        })
        .catch(err => {
          this.mySpinner.isLoading = false;
          console.log(err);
        });
    },
    getSellerInfo() {
      console.log(this.productMk.idSellerMember);
      let formData = new FormData();
      formData.append("idSellerMember", this.productMk.idSellerMember);
      axios
        .post(`${this.baseUrl}/product/postFindSellerMk.php`, formData)
        .then(res => {
          if (res.data.sellerUser !== 0) {
            this.sellerUser = res.data.sellerUser[0];
            console.log("sellerUser", this.sellerUser);
          }
        })
        .then(() => {
          this.getAllArea();
        })
        .catch(err => {
          this.mySpinner.isLoading = false;
          console.log(err);
        });
    },
    getAllArea() {
      axios
        .get(`${this.baseUrl}/getAllArea.php`)
        .then(res => {
          if (res.data.area !== 0) {
            let filter = res.data.area.filter((e, i) => {
              return e.idArea === this.sellerUser.vLinkAreaDetailId_MK;
            });
            this.area = {
              idArea: filter[0].idArea,
              areaName: filter[0].areaName,
              targetName: filter[0].targetName
            };
            console.log("area", this.area);
          }
        })
        .then(res => {
          this.getUserLL();
        })
        .catch(err => {
          this.mySpinner.isLoading = false;
          console.log(err);
        });
    },
    getUserLL() {
      let formData = new FormData();
      formData.append("idArea", this.area.idArea);
      axios
        .post(`${this.baseUrl}/product/getUserLL.php`, formData)
        .then(res => {
          if (res.data.targetLL !== 0) {
            this.target = res.data.targetLL[0];
            let lat = parseFloat(res.data.targetLL[0].lat);
            let long = parseFloat(res.data.targetLL[0].long);
            console.log("targetLL", this.target);
            this.center[0] = lat;
            this.center[1] = long;
            this.markers[0].id = this.target.idArea;
            this.markers[0].position = {
              lat: lat,
              lng: long
            };
            this.markers[0].tooltip = this.target.targetName;
            console.log("marker", this.markers[0]);
            this.centerUpdated(this.markers[0].position);
          }
        })
        .catch(err => {
          this.mySpinner.isLoading = false;
          console.log(err);
        });
    }
  },
  mounted() {
    this.mySpinner.isLoading = true;
    window.scroll(0, 0);
    this.queryId = this.$route.params.idProductOfMemberMk;
    this.getProductDetail(this.queryId);
  },
  created() {}
};
</script>

<style scoped></style>



