import axios from "axios";

const api = axios.create({
  baseURL: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
  headers: {
    "Content-Type": "application/json"
  }
});

export default {
  getUnitMk() {
    return api.get("product/getUnitMk.php");
  }
};



